.container {
  width: 100%;
  min-height: 100vh;
  padding: 60px 20px 30px;
  background: #232323;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center; }
  @media (min-width: 600px) {
    .container {
      padding: 0 60px 60px;
      justify-content: center; } }
  .container.justify-top {
    justify-content: flex-start;
    padding-top: 60px; }

.text-container {
  max-width: 75%;
  text-align: center; }
  @media (min-width: 600px) {
    .text-container {
      max-width: 60%; } }
  .text-container.border {
    border: 1px solid #fff;
    padding: 18px 20px; }

.title {
  font-size: 24px;
  line-height: 1.3;
  color: #fff; }
  @media (min-width: 600px) {
    .title {
      font-size: 40px; } }

.buttons-container {
  margin-top: 40px; }
  @media (min-width: 600px) {
    .buttons-container {
      margin-top: 60px; } }

.button {
  font-size: 18px;
  color: #fff;
  text-align: center;
  cursor: pointer;
  transition: all 0.25s cubic-bezier(0.42, 0, 0.58, 1); }
  .button:not(:last-child) {
    margin: 0 0 20px 0; }
  @media (min-width: 600px) {
    .button {
      font-size: 24px;
      display: inline-block; }
      .button:not(:last-child) {
        margin: 0 30px 0 0; } }
  .button.border {
    border: 1px solid #fff;
    padding: 8px 10px; }
    .button.border:hover {
      background: #fff; }
      .button.border:hover a {
        color: #005CAF; }
  .button a {
    color: #fff; }

.image-container {
  width: 100%;
  margin-top: 40px;
  box-shadow: 0 0 10px 10px #000000;
  cursor: pointer;
  transition: all 0.25 cubic-bezier(0.42, 0, 0.58, 1); }
  @media (min-width: 600px) {
    .image-container {
      width: 500px;
      margin-top: 60px; } }
  .image-container:hover {
    box-shadow: 0 0 10px 15px #000000; }
  .image-container img {
    width: 100%; }
