.container {
  height: 60px;
  padding: 0 20px;
  display: flex;
  align-items: center;
  background: #232323; }
  @media (min-width: 1024px) {
    .container {
      height: 75px;
      padding: 0 60px; } }

.inner {
  position: relative;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center; }

.logo {
  font-size: 28px;
  font-weight: bold;
  color: #fff;
  padding: 4px 8px; }
  .logo a {
    color: #fff; }

.menu-container .button {
  width: 34px;
  height: 34px;
  display: flex;
  justify-content: center;
  background: #232323; }

.menu-container ul.menu {
  margin-top: 30px;
  padding: 12px 10px;
  display: none;
  position: absolute;
  background: #232323;
  border: 1px solid #fff;
  right: 0; }
  .menu-container ul.menu.active {
    display: block; }

.menu-container li {
  font-size: 20px;
  color: #fff; }
  .menu-container li:not(:last-child) {
    margin-bottom: 10px; }
  .menu-container li a {
    color: #fff; }
